import { BrowserModule } from '@angular/platform-browser';
import { NgModule, ErrorHandler } from '@angular/core';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { MainPage } from '../pages/main/main';
import { ItemDetailsPage } from '../pages/item-details/item-details';
import { ListPage } from '../pages/list/list';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HttpModule } from '@angular/http';

//Ionic native
import { NativeStorage } from '@ionic-native/native-storage';

//My pages
import {MonanListPage} from '../pages/monan-list/monan-list';
import {MonanDataProvider } from '../providers/monan-data';
import {CuahangDataProvider } from '../providers/cuahang-data';
import {MonanDetailPage} from '../pages/monan-detail/monan-detail';
import {WelcomePage} from '../pages/welcome/welcome';
import {MonanFavoritePage} from '../pages/monan-favorite/monan-favorite';
import {ThitListPage} from '../pages/thit-list/thit-list';
import {CuahangListPage} from '../pages/cuahang-list/cuahang-list';
import {KhuyenmaiListPage} from '../pages/khuyenmai-list/khuyenmai-list';
import {CuahangMapPage} from '../pages/cuahang-map/cuahang-map';
import {CuahangTabsPage} from '../pages/cuahang-tabs/cuahang-tabs'
import { InitDataProvider } from '../providers/init-data';
import { Configs } from '../config';
import { GiaviDataProvider } from '../providers/giavi-data';
import { ThitDataProvider } from '../providers/thit-data';
import { FormBuilder } from '@angular/forms';


@NgModule({
  declarations: [
    MyApp,
    MainPage,
    ItemDetailsPage,
    ListPage,
    MonanListPage,
    MonanDetailPage,
    WelcomePage,
    MonanFavoritePage,
    ThitListPage,
    CuahangListPage,
    KhuyenmaiListPage,
    CuahangMapPage,
    CuahangTabsPage,

  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 


{
  links: [
    { loadChildren: '../pages/main/main.module#MainPageModule', name: 'MainPage', segment: 'main', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/monan-detail/monan-detail.module#MonanDetailPageModule', name: 'MonanDetailPage', segment: 'monan-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/monan-list/monan-list.module#MonanListPageModule', name: 'MonanListPage', segment: 'monan-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module#WelcomePageModule', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/monan-favorite/monan-favorite.module#MonanFavoritePageModule', name: 'MonanFavoritePage', segment: 'monan-favorite', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/thit-list/thit-list.module#ThitListPageModule', name: 'ThitListPage', segment: 'thit-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/khuyenmai-list/khuyenmai-list.module#KhuyenmaiListPageModule', name: 'KhuyenmaiListPage', segment: 'khuyenmai-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cuahang-list/cuahang-list.module#CuahangListPageModule', name: 'CuahangListPage', segment: 'cuahang-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cuahang-map/cuahang-map.module#CuahangMapPageModule', name: 'CuahangMapPage', segment: 'cuahang-map', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cuahang-tabs/cuahang-tabs.module#CuahangTabsPageModule', name: 'CuahangTabsPage', segment: 'cuahang-tabs', priority: 'low', defaultHistory: [] }
  ]
}),

  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    MainPage,
    ItemDetailsPage,
    ListPage,
    MonanListPage,
    MonanDetailPage,
    WelcomePage,
    MonanFavoritePage,
    ThitListPage,
    CuahangListPage,
    KhuyenmaiListPage,
    CuahangMapPage,
    CuahangTabsPage,
    
    
  ],
  providers: [
    StatusBar,
    SplashScreen,
    NativeStorage,
    FormBuilder,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    MonanDataProvider,
    CuahangDataProvider,
    InitDataProvider,
    Configs,
    GiaviDataProvider,
    ThitDataProvider 
  ]
})
export class AppModule {}



